.filter-bar {
    background: var(--primary-light);
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 20px;
    transition: var(--transition);
}

.filter-row {
    display: flex;
    gap: 20px;
    margin-bottom: 10px;
    flex-wrap: wrap;
}

.filter-row button {
    margin-right: 2px;
}
.filter-row button:last-child {
    margin-right: 0;
}

.search-input {
    padding: 10px 15px;
    border-radius: 6px;
    border: 1px solid var(--text-tertiary);
    outline: none;
    background: transparent;
    color: var(--text-primary);
    flex: 1;
    min-width: 150px;
    transition: var(--transition);
}

.search-input:focus {
    border-color: var(--primary);
}

.filter-button {
    padding: 8px 12px;
    border: 1px solid var(--text-tertiary);
    border-radius: 6px;
    background: transparent;
    color: var(--text-secondary);
    cursor: pointer;
    transition: var(--transition);
}

.filter-button.active {
    background: var(--primary);
    color: white;
    border-color: var(--primary);
}

.filter-button:hover:not(.active) {
    background: var(--primary-light);
    border-color: var(--primary);
    color: var(--primary);
}